.block-card {
  background-color: #fff;
  box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
  border-radius: 16px;
  min-width: 260px;

  &__number {
    display: inline-block;
    background-color: #e5e5e5;
    border: 1px solid transparent;
    border-radius: 23px;
    margin-bottom: 24px;
    padding: 2px 12px;

    &--style {
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 13px;
      line-height: 20px;
      text-transform: uppercase;
      color: #2060f6;
    }

  }

  &__text {
    margin-bottom: 32px;
  }

  &__link {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    color: #334563;
  }

  &__link-arrow {
    background-color: #2060f6;
    padding: 10px;
    border-radius: 50%;
    margin-right: 16px;

    @include hover(background-color, aqua);
  }

  &--number-1 {
    @include onTablet {
      @include hover(transform, translateY(-5%));
    }
  }

  &--number-2 {
    @include onTablet {
      transform: translateY(-50%);
      @include hover(transform, translateY(-55%));
    }
  }

  &--number-3 {
    @include onTablet {
      @include hover(transform, translateY(-5%));
    }
  }

  &--number-4 {
    @include onTablet {
      transform: translateY(-50%);
      @include hover(transform, translateY(-55%));
    }
  }
}
