.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  border-radius: 16px;
  margin-bottom: 24px;
  @include hover(transform, translateY(-5%));

  @include onSmallDesktop {
    width: 365px;
  }

  &__photo-container {
    display: block;
    position: relative;
    box-sizing: border-box;
    width: 110px;
    height: 110px;
    margin: 70px 0;
  }

  &__back-left {
    position: absolute;
    height: 110px;
    width: 45px;
    background-color: $colorBlue;
    border-radius: 12px;
    transform: translateY(-30%) rotate(30deg);
  }

  &__back-right {
    position: absolute;
    right: 0;
    height: 110px;
    width: 45px;
    background-color: $colorBlue;
    border-radius: 12px;
    transform: translateY(30%) rotate(30deg);
  }

  &__photo {
    position: absolute;
    height: 100%;
    width: 100%;
    
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
  }

  &__logo {
    width: 22px;
    height: 22px;
    background-image: url(../images/testimonials/“card_logo.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 12px;
  }

  &__discription {
    text-align: center;
    padding: 0 42px;
    margin-bottom: 16px;

    @include onTablet {
      padding: 0 98px;
    }

    @include onSmallDesktop {
      padding: 0 48px;
    }
  }

  &__name {
    color: #253757;
    margin-bottom: 4px;
  }

  &__role {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #c0cdd7;
    margin-bottom: 32px;
  }
}
