.nav {
  width: 100%;

  &__list {
    display: flex;
  }

  &__item {
    margin-left: 60px;
    list-style: none;
  }

  &__link {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    @include hover(border-bottom, 1px solid #fff);
  }
}